'use client'
import { useAuth } from "@/components/AuthContext"
import { useRouter } from "next/navigation"
import { useEffect } from "react"

export default function Page(){
       const {user} = useAuth()
       const router = useRouter()
       useEffect(()=>{
          if(user?.role!='admin'){
             router.push('/dashboard')
          }
    
       },[user])
    return(
        <>User list</>
    )
}